<template>
    <tr>
        <div v-if="!updated">
            <div>
                <span>{{reply.writer}}</span>
                (<span>{{reply.writetime | toDate}}</span>)
            </div>
            <div class="row">
                <div class="col-10">
                    <h5 class="card-text">{{reply.content}}</h5>
                </div>
                <div class="col-2">
                    <div class="row">
                        <button class="btn btn-primary" @click="toggleComment()">수정</button>
                        <button class="btn btn-primary" @click="deleteComment(bno)">삭제</button>
                    </div>
                </div>
            </div>
        </div>
        <div v-else>
            <div class="row">
                <p class="col-2">댓글수정</p>
                <span class="col-2">작성자: {{reply.writer}}</span>
            </div>
            <div class="form-group">
                <textarea class="form-control" id="content" v-model="reply.content" ref="content" style="min-width: 100%" @keypress.enter="updateComment()"></textarea>
            </div>
            <div class="text-right">
                <button class  ="btn btn-primary" @click="toggleComment()">취소</button>
                <button class  ="btn btn-primary" @click="updateComment(bno)">등록</button>
            </div>
        </div>
    </tr>
</template>

<script>
import moment from "moment";
import axios from 'axios';
import {bus} from '@/boardbus'
export default {
    name: "Comment",
    props: ['reply', 'bno'],
    data(){
        return{
            updated: false,
        }
    },
    methods:{
        toggleComment(){
            if(this.updated){
                this.updated  = false;
            }else{
                this.updated = true;
            }
        },
        deleteComment(bno){
            console.log(bno);
            axios
            .delete(`http://localhost:9999/vue/api/board/reply/${this.reply.no}`)
            .then(({data}) => {
                console.log(data);
                console.log(bno);
                this.$router.push('/search');
                setTimeout(()=>{
                    bus.$emit('searchBoard', bno)
                }, 100);
            })
            .catch(()=>{
                alert("오류 발생");
            })
        },
        updateComment(bno){
            console.log(bno);
            axios
            .put(`http://localhost:9999/vue/api/board/reply`,{
                bno: bno,
                no: this.reply.no,
                write: this.reply.write,
                content: this.reply.content
            })
            .then(({data}) => {
                console.log(data);
                // this.$router.push('/search');
                setTimeout(()=>{
                    bus.$emit('searchBoard', bno)
                    this.updated = false;
                }, 100);
            })
            .catch(() => {
                alert("오류 발생");
            });
        }
    },
    filters: {
        toDate: function (regtime) {
            return moment(new Date(regtime)).format("YYYY.MM.DD.HH:mm");
        }
    },
};
</script>

