<template>
  <div>
    <h4 class="text-center">게시글 내용</h4>
    <table class="table table-condensed" v-if="board">
      <tr>
        <th>번호</th>
        <td v-text="board.no"></td>
      </tr>
      <tr>
        <th>글쓴이</th>
        <td v-text="board.writer"></td>
      </tr>
      <tr>
        <th>제목</th>
        <td v-text="board.title"></td>
      </tr>
      <tr>
        <th>날짜</th>
        <td>{{ board.regtime | toDate }}</td>
      </tr>
      <tr>
        <td colspan="2" v-text="board.content"></td>
      </tr>
    </table>

    <div class="text-right">
      <router-link class="btn btn-primary" to="/list">목록</router-link>
      <button class="btn btn-primary" @click="updateBoard()">수정</button>
      <button class="btn btn-primary" @click="removeBoard()">삭제</button>
    </div>

    <div style="margin-top: 40px;">
      <h4 class="text-center">댓글</h4>
      <table class="table" width="100%">
        <comment v-for="(reply, index) in replies" :reply="reply" :key="index" :bno="board.no"></comment>
        <tr>
          <div class="row">
            <p class="col-2">댓글등록하기</p>
            <span class="col-2">작성자: ssafy</span>
          </div>
          <div class="form-group">
            <textarea class="form-control" id="content" v-model="content" ref="content" style="min-width: 100%" @keypress.enter="insertComment()"></textarea>
          </div>
          <div class="text-right">
              <button class  ="btn btn-primary" @click="insertComment()">등록하기</button>
            </div>
        </tr>
      </table>
    </div>
  </div>
</template>
<script>
import axios from "axios";
import moment from "moment";
import {bus} from '@/boardbus'
import Comment from '@/components/Comment.vue';

export default {
  components: {
    Comment
  },
  data() {
    return { 
      board: {},
      replies: [],
      content: '',
    };
  },
  filters: {
    toDate: function (regtime) {
      return moment(new Date(regtime)).format("YYYY.MM.DD");
    }
  },
  created() {
    bus.$on('searchBoard',this.searchBoard)
  },
  methods:{
    updateBoard(){
      this.$router.push('/update')
      setTimeout(()=>{
        bus.$emit('updateBoard',this.board)
      },100)
    },
    removeBoard(){
      this.$router.push('/remove')
      setTimeout(()=>{
        bus.$emit('removeBaord',this.board.no)
      },100)
    },
    searchBoard(no){
      console.log('searchboard.................',no)
      axios
      .get(`http://localhost:9999/vue/api/board/${no}`)
      .then(({ data }) => {
        this.board = data;
        this.replies = data.replies;
        console.log(this.replies);
      })
      .catch(err => {
        console.log(err);
        alert("게시글 조회 중 오류 발생");
      });
    },
    insertComment(){
      axios
      .post(`http://localhost:9999/vue/api/board/reply`,{
        no: 0,
        bno: this.board.no,
        writer: "ssafy",
        content: this.content
      })
      .then(({data}) => {
        if(data == "success"){
          alert("댓글 등록 완료.");
          this.$router.push('/search');
          setTimeout(()=>{
            bus.$emit('searchBoard', this.board.no)
          }, 100);
        } else{
          alert("등록 실패");
        }
      })
      .catch(() => {
        alert("댓글 등록 실패");
      })
    }
  }
};
</script>
